@import "../../assets/scss/mixin";
@import "../../assets/scss/variables";
@import "../../assets/scss/templates";

.wrapper {
  position: relative;
  @include wrapper();
  @extend %flexAlignCenterJustSB;
  width: 100%;

  .content {
    @include container();
    @include content();

    h1 {
      color: $textColor;
      font-weight: 900;
      line-height: 5rem;
      font-size: 100px;
    }

    .header {
      @include header();
    }
  }

  @media (max-width: 1000px) {
    justify-content: center;

    .animation {
      display: none;
    }
  }
}


.greyText {
  font-size: 1.6rem;
  letter-spacing: 1px;
  margin-top: 3rem;
  display: block;
  color: #8d8d8d;
}

.blueBg {
  color: #f0f2f5;
  background-color: $focusColor;
}

.letters {
  width: 900px;
  display: flex;
  justify-content: center;
  overflow: hidden;
  font-size: 30px;
  color: black;
}

.video {
  object-fit: cover;
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: -1;
}


@media (max-width: 1200px) {
  .letters {
    justify-content: flex-start;
    font-size: 50px;
  }


}

@media (max-width: 700px) {
  .letters {
    max-width: 700px;
    font-size: 30px;
  }
}

@media (max-width: 400px) {
  .letters {
    font-size: 25px;
  }
}

.videoGif {
  object-fit: cover;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
}